.post {
  max-width: 800px; }
  .post .markdown code:not(.hljs) {
    color: #f14e32;
    background: #eee;
    padding: 2px 6px;
    font-size: 13px; }
  .post .markdown pre {
    display: block;
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 0.9em;
    white-space: pre;
    overflow-x: auto; }
    .post .markdown pre code {
      padding: 0.7em; }
  .post .markdown ul {
    padding-left: 20px; }



